.reasons{
    padding: 0 2rem;
    display: flex;
    gap: 2rem;
}

.left-reasons{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}

.left-reasons>img{
    object-fit: cover;
}

.left-reasons>:nth-child(1){
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
}

.left-reasons>:nth-child(2){
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}

.left-reasons>:nth-child(3){
    width: 14rem;
    grid-column: 2/3;
    grid-row: 2;
}

.left-reasons>:nth-child(4){
    width: 10rem;
    grid-row: 2;
    grid-column: 3/4;
    height: 11.2rem;
}

.right-reasons{
    flex: 1;
    text-transform: uppercase;
    display: flex;
    gap: 1rem;
    flex-direction: column;
}

.right-reasons>span{
    font-weight: bolder;
    color: var(--orange);
}

.right-reasons>div{
    color: white;
    font-weight: bolder;
    font-size: 3rem;
}

.details-right{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.details-right>div{
    display: flex;
    font-size: 1rem;
    gap: 1rem;
}

.details-right>div>img{
    width: 2rem;
    height: 2rem;
}

.partners{
    display: flex;
    gap: 1rem;
}

.partners>img{
    width: 2.5rem;
}

@media screen and (max-width: 768px) {
    .reasons{
        flex-direction: column;
    }

    .left-reasons{
        grid-auto-rows: auto;
        overflow: hidden;
    }

    .left-reasons>:nth-child(1){
        width: 7rem;
        height: 17rem;
    }
    .left-reasons>:nth-child(2){
        width: 15rem;
        height: 10rem;
    }
    .left-reasons>:nth-child(3){
        width: 7rem;
        height: 6rem;
    }
    .left-reasons>:nth-child(4){
        width: 7rem;
        height: 6rem;
    }
    
}